import React, { useEffect, useState } from 'react'

const InputImage = ({
    isEmpty,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    title
}) => {
    const [hover, setHover] = useState()
    const show = uom === 'gram' || uom === 'kg' ? false : true

    return (
        <div name='upload' className={` ${inputWidth} mb-3 mr-3 ${show ? null : 'd-none'}`} onClick={() => hover && values ? onChange() : null}>
            <div className={` pb-1 LatoBold text-nowrap ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div
                onMouseLeave={() => setHover()}
                onMouseEnter={() => setHover(true)}
                style={{
                    backgroundImage: `url(${values})`,
                    backgroundSize: 'cover',
                    opacity: 0.7,
                    width: values ? 42 : false
                }}
                id='input-img'
                className={`${isEmpty ? 'border-red' : 'border'} rounded border-soft-green bg-white d-flex w-fit-content px-2 justify-content-center align-items-center input-height d-flex mr-3`}
            >
                {
                    values && hover ?
                        <div className="hand-cursor" style={{ margin: 0 }}>
                            <span style={{ fontSize: values ? 25 : false }} className={`photo_text hidden-xs ${values ? (hover ? 'iconU-entryTrash css-2613qy-menu' : null) : " iconU-entryPhotos"}`} />
                        </div>
                        :
                        <label className="hand-cursor" style={{ margin: 0 }}>
                            <input name='upload' type="file" nv-file-select uploader="$ctrl.uploader" accept="image/x-png,image/gif,image/jpeg" onChange={(e) => onChange(e)} />
                            <span className="fa fa-camera" />
                            <span style={{ fontSize: values ? 25 : false }} className={`photo_text hidden-xs ${values ? (hover ? 'iconU-entryTrash css-2613qy-menu' : null) : " iconU-entryPhotos"}`} />
                        </label>
                }
            </div>
        </div>
    )
}

export default InputImage