import React from 'react'
import Dropdown from 'Component/Dropdown'
import Decimal from 'Component/Input/decimal'
import Input from 'Component/Input'
import Note from 'Component/Input/Note'
import InputImage from 'Component/Input/InputImage'


const WeightEntry = ({
    entryListIdx,
    wmsPoSkuInfoId,
    gradeList,
    ripeness,
    inputWidth,
    showTitle,
    item,
    onChange,
    onChangeDropdown,
    uom,
    poListIdx,
    category
}) => {
    return (
        <div className='d-flex w-100 we-m'>
            <Input
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                showTitle={showTitle}
                title={'Input Quantity'}
                onChange={(e) => onChange(e)} />

            <Decimal
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                title={'Input Weight'}
                showTitle={showTitle}
                onChange={(e) => onChange(e)} />
            <Dropdown
                isEmpty={item.wasteTypeIsEmpty}
                selectedValue={item?.wasteType}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={true}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'grade' })}
                title='Waste'
                placeholder='Select Waste'
                options={gradeList}
                width='w-30'
                showTitle={showTitle}
                className={null} />
            <Note
                isEmpty={item.noteIsEmpty}
                grade={'Waste'}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                inputWidth={'w-30'}
                values={item?.note}
                showTitle={showTitle}
                title={'Note'}
                onChange={(e) => onChange(e)} />
            <InputImage
                isEmpty={item.uploadPhotoIsEmpty}
                uom={'PACK'}
                inputWidth={'fit-content'}
                values={item.uploadPhoto}
                onChange={(e) => onChange(e)}
                showTitle={true}
                title={'Add Photos'} />
        </div>
    )
}
export default WeightEntry